body {
  background-color: #060607;
  width: 100vw;
  margin: 0;
  padding: 0;
  font-size: 14px;
  overflow: auto;
  overflow-x: hidden !important;
}

::-webkit-scrollbar {
  width: 1.2em;
}

::-webkit-scrollbar-track {
  background: #1a1631;
}

::-webkit-scrollbar-thumb {
  background: #433d6980;
  border: .2em solid #1a1631;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #433d69cc;
}

::-webkit-scrollbar-thumb:active {
  background: #433d69;
}

a {
  color: inherit;
  pointer-events: auto;
  text-decoration: none;
}

ul {
  margin: 0;
}

canvas {
  background-color: #0000 !important;
}

.button {
  color: #b6abff;
  pointer-events: auto;
  background-color: #0000;
  border: 1px solid #5346a5;
  border-radius: 0;
  justify-content: center;
  align-items: center;
  gap: .6em;
  padding: .6em .9em;
  font-family: Roboto;
  font-size: 1em;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  transition: all .15s;
  display: inline-flex;
}

#contact-form button {
  color: #ffffffde;
  pointer-events: auto;
  cursor: pointer;
  background-color: #5346a5;
  border: 1px solid #5346a5;
  border-radius: 0;
  justify-content: center;
  align-items: center;
  gap: .6em;
  padding: .6em .9em;
  font-family: Roboto;
  font-size: 1em;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  transition: all .15s;
  display: inline-flex;
}

#contact-form button:hover {
  color: #ffffffde;
  background-color: #5346a5;
  border: 1px solid #5346a5;
  transition: all .15s;
}

.button img {
  width: 1em;
}

.button:hover {
  color: #ffffffde;
  background-color: #5346a5;
  border: 1px solid #5346a5;
  transition: all .15s;
}

#artboard {
  z-index: 10;
  pointer-events: none;
  opacity: 0;
  position: fixed;
  bottom: 0;
  left: 50vw;
  transform: translateX(-50%);
}

@keyframes sizeChange {
  0%, 100% {
    width: 52px;
    height: 52px;
  }

  50% {
    width: 60px;
    height: 60px;
  }
}

.start-button-fade-in {
  transition: all .5s;
  opacity: 1 !important;
}

#start-button-container {
  z-index: 500;
  opacity: 0;
  width: 50px;
  height: 50px;
  margin: 0 0 3vh;
  transition: all .5s;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
}

#start-button {
  pointer-events: auto;
  background-color: #4aff396c;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: 1.3s infinite sizeChange;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#start-button div {
  background-color: #49ff39;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#loader {
  z-index: 200;
  opacity: 1;
  flex-direction: column;
  gap: .5em;
  width: 10vw;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader-fade-out {
  transition: all .3s;
  opacity: 0 !important;
  width: 0 !important;
}

#loading-bar-container {
  background-color: #1a1631;
  border-radius: .2em;
  width: 100%;
  height: .4em;
}

#loading-text {
  color: #5346a5;
  text-align: center;
  opacity: .5;
  width: 100%;
  font-family: Montserrat;
  font-size: 2em;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

#loading-bar {
  background: linear-gradient(90deg, #6e59fc 0%, #a89bff 102.8%);
  border-radius: .2em;
  width: 0%;
  height: 100%;
  box-shadow: 0 0 12px #7f6fe980;
}

.div-fade-in {
  transition-property: transform, opacity;
  transition-duration: .2s;
  transition-timing-function: ease-out;
  transform: translateY(50px);
  opacity: 0 !important;
}

.div-fade-out {
  transition-property: transform, opacity;
  transition-duration: .4s;
  transition-timing-function: ease-out;
  opacity: 0 !important;
}

.artboard-fade-out-mobile {
  transform-origin: 0 100%;
  transition-property: transform, opacity, scale;
  transition-duration: .4s;
  transition-timing-function: ease-out;
  scale: .98;
  opacity: 0 !important;
}

.artboard-fade-in-mobile {
  transform-origin: 0 100%;
  transition-property: transform, opacity, scale;
  transition-duration: .4s;
  transition-timing-function: ease-out;
  scale: 1;
  opacity: 1 !important;
}

.div-in-view {
  opacity: 1;
  transition-property: transform, opacity;
  transition-duration: .5s;
  transition-timing-function: ease-out;
  transform: none;
}

.central-left-div-load-animation, .central-right-div-load-animation {
  transition: all 1.1s;
  opacity: 1 !important;
  transform: translateY(0) !important;
}

.bg-banner-load-animation {
  transition: all 2.2s;
  opacity: .12 !important;
}

.nav-load-animation {
  transition: all 1.3s;
  opacity: 1 !important;
}

.background-load-animation {
  transition: all .5s;
  opacity: 1 !important;
}

#backgrounds {
  opacity: 0;
}

#project-purple-background {
  z-index: 20;
  pointer-events: none;
  opacity: 0;
  background-color: #0e0321;
