dialog {
  color: #fffc;
  background: none;
  border: none;
  border-top-left-radius: 1.2em;
  border-top-right-radius: 1.2em;
  width: 60vw;
  margin-top: 3vh;
  margin-bottom: 10vh;
  padding: 0;
  overflow-x: hidden;
}

dialog:focus {
  outline: none;
}

dialog[open] {
  -webkit-animation: .3s ease-out show;
}

@-webkit-keyframes show {
  from {
    opacity: 0;
    transform: translateY(2.5%);
  }

  to {
    opacity: 1;
    transform: translateY(0%);
  }
}

dialog[open]::backdrop {
  -webkit-animation: .2s ease-out show-backdrop;
}

dialog.hide::backdrop {
  -webkit-animation: .2s ease-out hide-backdrop;
}

@-webkit-keyframes show-backdrop {
  from {
    opacity: 0;
  }

  to {
    opacity: .75;
  }
}

@-webkit-keyframes hide-backdrop {
  to {
    opacity: 0;
  }
}

dialog.hide {
  -webkit-animation: .3s ease-out hide;
}

@-webkit-keyframes hide {
  to {
    opacity: 0;
    transform: translateY(2.5%);
  }
}

