.project-dialog-close-icon {
    background:none;
    color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

dialog::backdrop {
    background-color: rgb(0, 0, 0);
    opacity: 0.75;
    -webkit-animation: none;
}

dialog {
    width:60vw;
    background: none;
    color: rgba(255, 255, 255, 0.8);
    padding:0;
    margin-top : 3vh;
    margin-bottom:10vh;
    border:none;
    overflow-x: hidden;
    border-top-left-radius: 1.2em;
    border-top-right-radius: 1.2em;
}

dialog:focus {
    outline : none;
}

dialog[open] {
    -webkit-animation: show 0.3s ease-out;
}
@-webkit-keyframes show{
    from {
        transform: translateY(2.5%);
        opacity:0;
    }
    to {
        transform: translateY(0%);
        opacity:1;
    }
}

dialog[open]::backdrop {
    -webkit-animation: show-backdrop 0.2s ease-out;
}
dialog.hide::backdrop {
    -webkit-animation: hide-backdrop 0.2s ease-out;
}
@-webkit-keyframes show-backdrop {
    from {
        opacity: 0;
    }
    to {
        opacity: 0.75;
    }
}
@-webkit-keyframes hide-backdrop{
    to {
        opacity: 0;
    }
}

dialog.hide {
    -webkit-animation: hide 0.3s ease-out;
}
@-webkit-keyframes hide{
    to {
        transform: translateY(2.5%);
        opacity:0;
    }
}


