/* media queries ___________________________________________________________________________________________________________ */

@media (max-width:600px) {
    body {
        font-size:16px;
    }
    #artboard {
        position:fixed;
    }
    #backgrounds {
        position: fixed;
        top:0;
    }

    #loader {
        width:7em;
    }
    #loading-bar-container {
        height:0.2em;
        width:100%;
        background-color: #1A1631;
        border-radius: 0.2em;
    }
    #loading-text {
        font-size: 1.4em;
    }

    #top-nav {
        width:100%;
        padding:2.5em 2.5em;
        background: linear-gradient(180deg, #0A0217 0%, rgba(14, 3, 33, 0.00) 100%);
        align-items: center;
    }
    #top-nav-hamburger {
        display:block;
        width:28px;
        pointer-events: all;
    }
    #nav-right-panel {
        position:absolute;
        top:0;
        right:0;
        padding:2.6em 2.5em 0 0;
        align-items: flex-end;
        font-size: 1.1em;
        gap:3vh;
        height:100vh;
        width:15em;
        background: linear-gradient(270deg, #090714 0%, rgba(10, 7, 21, 0.87) 51%, rgba(10, 8, 21, 0.70) 75%, rgba(10, 8, 21, 0.00) 100%);
        transition:opacity 0.4s;

    }
    .nav-right-panel-hidden {
        opacity:0;
        transform:translateX(100em);
        transition:opacity 0.4s;
    }
    .nav-module:hover:not(.active) {
        border: none;
        color:#ffffff5c;
    }
    #nav-right-panel img {
        width:24px;
        display: block;
        pointer-events: all;
        z-index: 1000;
    }
    #nav-right-panel li:first-of-type {
        display:flex;
        flex-direction: column;
        align-items: flex-end;
        gap:2em;
    }
    .active {
        border:none !important;
    }
    .active:hover {
        border:none !important;
    }

    #website-logo {
